html {
  height: 100%;
  box-sizing: border-box;
  font-size: calc(15px + 0.390625vw);
}
*,
*:before,
*:after {
  box-sizing: inherit;
  padding: 0;
  margin: 0;
  &:focus {
    outline: none;
  }
}
body {
  height: 100%;
  background-color: var(--main-color);
  font-family: Roboto, 'Helvetica Neue', sans-serif;
}
#root {
  height: 100%;
}
.app {
  background-color: rgb(55, 66, 65);
  color: bisque;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.form-control {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-weight: 600;
  font: inherit;
  padding: 0.1rem;
  border-radius: 4px;
  border: 1px solid black;
  background-color: bisque;
  text-align: center;
}
.btn {
  cursor: pointer;
  text-decoration: none;
  border-radius: 4px;
  background-color: teal;
  border: none;
  padding: 20px;
  color: bisque;
}
.control {
  display: flex;
  flex-direction: column;
}
